    title: '2016.9 - 至今 深圳法狗狗担任前端技术负责人',
    content: '6\u6708\u4EFD\u62FF\u5230\u6BD5\u4E1A\u8BC1\u540E\uFF0C\u6211\u5C31\u56DE\u5BB62\u4E2A\u591A\u6708\uFF0C\u671F\u95F4\u8003\u8651\u7740\u53BB\u8003\u516C\u52A1\u5458\uFF0C\u6298\u817E\u4E86\u8BB8\u4E45\uFF0C\u653E\u4E0D\u4E0B\u524D\u7AEF\uFF0C\u594B\u4E0D\u987E\u8EAB\u7684\u6765\u5230\u4E86\u6DF1\u5733\uFF0C\u5F00\u59CB\u4E86\u6211\u7684\u65B0\u5F81\u7A0B\u3002\n\n    \u6709\u5E78\u548C\u6CD5\u72D7\u72D7\u7684CTO\u3001CEO\u804A\u5929\uFF0C\u88AB\u68A6\u60F3\u611F\u52A8\u5230\uFF0C\u4E0D\u8FC7\u4E5F\u975E\u5E38\u6709\u6311\u6218\uFF0C\u56E0\u4E3A\u6CD5\u72D7\u72D7\u521A\u521A\u6210\u7ACB\uFF0C\u8FD8\u6CA1\u6709\u524D\u7AEF\u4EA7\u54C1\uFF0C\u9700\u8981\u6211\u4E00\u4E2A\u4EBA\u4ECE\u96F6\u5F00\u59CB\u524D\u7AEF\u9879\u76EE\u3002\u6211\u62C5\u5FC3\u8FC7\u81EA\u5DF1\u662F\u5426\u53EF\u4EE5\u80DC\u4EFB\uFF0C\u6700\u7EC8\u52C7\u6C14\u6218\u80DC\u4E86\u6050\u60E7\u3002\n\n    \u73B0\u5728\u5DF2\u7ECF\u8FC7\u4E861\u5E74\u4E86\uFF0C\u4EA7\u54C1\u4E5F\u8FED\u4EE3\u4E86\u6570\u5341\u6B21\uFF0C\u524D\u7AEF\u4EA7\u54C1\u7EBF\u4E5F\u6709\u597D\u51E0\u6761\uFF0C\u516C\u53F8\u878D\u8D44\u4E5F\u6210\u529F\u4E86\uFF0C\u56E2\u961F\u4ECE\u5F53\u521D\u7684\u6211\u548CCTO2\u4EBA\u6162\u6162\u7684\u53D1\u5C55\u8D77\u6765\u3002\n\n    \u73B0\u5728\u8FD8\u662F\u91C7\u7528react\u6280\u672F\u6808\u5F00\u53D1\uFF0C\u81F3\u4ECA\u6211\u4ECD\u662F\u524D\u7AEF\u5149\u6746\u53F8\u4EE4\uFF0C\u4E00\u4E2A\u4EBA\u8D1F\u8D23\u524D\u7AEF\u591A\u4E2A\u4EA7\u54C1\u7EBF\u7684\u5F00\u53D1\u548C\u7EF4\u62A4\uFF0C\u6709\u65F6\u5019\u6211\u4E5F\u4F1A\u62B1\u6028\u8FD9\u4E9B\u5DE5\u4F5C\u91CF\u592A\u591A\uFF0C\u4F46\u66F4\u591A\u7684\u662F\u601D\u8003\u5982\u4F55\u5C06\u8FD9\u4E9B\u4EA7\u54C1\u8FDB\u884C\u66F4\u4E3A\u6709\u6548\u7684\u7EF4\u62A4\uFF0C\u9047\u5230\u7684\u6240\u6709\u524D\u7AEF\u95EE\u9898\u53EA\u80FD\u9760\u81EA\u5DF1\u89E3\u51B3\uFF0C\u7B97\u6CD5\u65B9\u9762\u7684\u53EF\u4EE5\u627ECTO\u5E2E\u5FD9\u3002\n    '
